.header-top {
    padding: 17px 0;
    box-shadow: 0 3px 15px rgba(157, 157, 157, 0.14);
}

.header-lk {
    padding: 3px 0;
    box-shadow: 0 1px 5px rgba(157, 157, 157, 0.14);
    position: relative;
    z-index: 60;
    background: #fff;

}

.logo_wrp {
    display: flex;
    justify-content: space-between;
    align-items: center;

}



.logo-top {
    display: flex;
    align-items: center;
    flex-basis: 85%;

}

.logo-top__img {
    flex-basis: 15%;
    display: inline-block;
}

.logo-top__title {
    font-size: 40px;
    font-weight: 700;
    color: $fontd;
    flex-basis: 75%;
    margin-left: 10px;
    margin-right: 10px;

}

.header-btn {

    height: 48px;
    font-size: 16px;
    color: #3a499d;
    background-color: #e7e7e7;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    font-size: 16px;
    line-height: 100%;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    padding: 0 1.9vw;
    transition: background .3s ease, color .3s ease, outline .3s ease, opacity .3s ease;
    cursor: pointer;
    height: 52px;
    white-space: nowrap;
    color: #3a499d;
    border: 0;
    position: relative;
    z-index: 50;

    &:hover {
        color: #273583;
        background-color: rgba(102, 120, 143, .3);
    }

}


.btn-fiolett {
    background: #3A499D;
    border-radius: 4px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    padding: 15px 30px;

    &:hover {
        background: #4F5FB7;
        color: #fff;
    }
}

.logo-top__img--lk {
    img {
        max-height: 38px;
    }
}

@media only screen and (max-width : 848px) {
    .logo-top__title {
        font-size: 30px;


    }

}

@media only screen and (max-width : 670px) {
    .logo-top__img {
        margin-right: 15px;
        max-width: 60px;
        min-width: 60px;
    }

    .logo-top__title {
        font-size: 18px;
        margin-left: 10px;
    }

}
