.wrp-links {
    display: flex;
    flex-direction: column;

    a {
        margin: 10px;
        font-size: 25px;
    }
}

.section-message {
    background: #F5FAFF;
    min-height: calc(100vh - 96px);
    position: relative;
}

.map-wrp {
    margin: 20px 0;
    width: 100%;
}

.message-wrp {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    max-width: 800px;
    margin: auto;


}

.bg_img {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 10;

    img {
        display: block;
    }
}

.message {
    display: flex;
    align-items: center;
    background: #fff;
    padding: 30px;
    border: 2px solid #ebeef5;
    margin-top: 100px;
    position: relative;
    z-index: 50;
    width: 100%;
    color: #000234;
    line-height: 1.4;
    margin-bottom: 20px;



    &__img {
        flex-basis: 15%;
        flex-shrink: 0;
        margin-right: 10px;

        img {
            max-width: 100px;
        }

    }

    &__info {
        flex-basis: 85%;
        line-height: 1.4;
    }

    &__green {
        color: green;
    }

    &__red {
        color: red;
    }

    &__title {
        font-size: 28px;
        font-weight: 500;
        margin-bottom: 20px;

    }

    &__subtitle {
        font-size: 18px;
        margin-bottom: 20px;
    }
}

.message-status {
    margin-top: 20px;
    align-items: flex-start;


    .message__img--status {
        flex-basis: 40%;


        img {
            max-width: 100%;
        }

        picture {
            max-width: 100%;
        }
    }

    .message__info--status {
        flex-basis: 60%;
        margin-left: 20px;
    }
}





@media only screen and (max-width : 576px) {
    .message {
        &__title {
            font-size: 20px;
        }

        &__subtitle {
            font-size: 16px;
        }
    }
}

@media only screen and (max-width : 470px) {
    .message-status {
        flex-wrap: wrap;

        .message__img--status {
            flex-basis: 100%;
            margin-left: 0;
            margin-bottom: 20px;
        }

        .message__info--status {
            flex-basis: 100%;
        }
    }


}
