.section_lk {
    background: #F5FAFF;
    min-height: 100vh;
    position: relative;


}

.lk_wrp {
    padding: 80px 0;
    position: relative;
    z-index: 60
}

.bg_img {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1;

    img {
        display: block;
    }
}

.header-lk__btns {
    display: flex;
}

.btn-lk {
    display: flex;
    margin-right: 30px;
    align-items: center;
    font-size: 16px;
    box-shadow: none;
    border: 2px solid $blue;
    background: #fff;
    color: $blue;
    padding: 7px 12px;
    border-radius: 4px;
    transition: all .3s;

    .ic_lk {
        width: 20px;
        height: 20px;
        fill: $blue;
        margin-right: 10px;
    }



    &:last-child {
        margin-right: 0;
    }

    &:hover {
        background: $blue;
        color: #fff
    }

    &:hover .ic_lk {
        fill: #fff;
    }
}


.ic-refresh {
    transform: rotate(230deg);
}

.qr-inputs__group {
    display: flex;
    align-items: center;
    margin: 15px 0;

    label {
        margin-right: 15px;
        font-size: 16px;
        color: $blue;
        flex-basis: 50%;
    }

    input,
    textarea,
    select,
        {
        font-size: 16px;
        padding: 16px 10px;
        width: 100%;
        background: #fff;
        color: $blue;
        border-radius: 4px;
        border: 1px solid #ececec;
        line-height: 1.3;

    }

    span {
        font-size: 16px;
        margin: 15px 0;
        color: #3A499D;
    }

}

.qr-btns {
    display: flex;
    margin-bottom: 20px;
    margin-top: 20px
}

.qr-btn {
    color: #001837;
    border-radius: 40px;
    border: 1px solid #c1c1c1;
    padding: 15px 30px;
    background: #fff;
    margin-right: 10px;
    transition: all .3s;

    &:last-child {
        margin-right: 0;
    }

    &:hover {
        background: #616681;
        color: #fff;
        border: 1px solid #616681;
    }
}

.qr-btn--reverse {
    color: #fff;
    border: 1px solid #616681;
    background: #616681;

    &:hover {
        background: #fff;
        color: #001837;
        border: 1px solid #c1c1c1;
    }
}

.qr-image img {
    max-height: 250px;
}

@media only screen and (max-width : 994px) {
    .qr-btns {
        flex-wrap: wrap;

    }

    .qr-btn {
        margin-bottom: 15px;
    }
}

@media only screen and (max-width : 520px) {
    .btn-lk {
        margin-right: 10px;
        padding: 10px 10px;

        .ic_lk {
            width: 15px;
            height: 15px;
            fill: $blue;
            margin-right: 0px;
        }
    }

    .btn-lk span {
        display: none;
    }


}


@media only screen and (max-width : 442px) {
    .qr-inputs__group {


        label {
            margin-right: 10px;
            font-size: 12px;
            flex-basis: 50%;
            line-height: 1.4;
        }

        input,
        textarea {
            font-size: 14px;

        }
    }

    .qr-btns {
        flex-wrap: wrap;
        margin: 15px 0;
    }




}
